import axios from 'axios'

export const register = newUser => {
    console.log(newUser)
    return axios
        .post('users/register', {
            fName: newUser.first_name,
            lName: newUser.last_name,
            username: newUser.username,
            passwordHash: newUser.password,
            dev_pass: newUser.dev_pass,
            consentBool: newUser.consentBool
        })
        .then(res => {
            console.log(res)
        })
        .catch(err => {
            //console.log("FUCK: " + err)
        })
}

export const login = user => {
    return axios
        .post('users/login', {
            username: user.username,
            passwordHash: user.passwordHash
        })
        .then(res => {
            localStorage.setItem("usertoken", res.data)
            return res.data
        })
        .catch(err => {
	    //alert("Login information incorrect! Please try again!")
            console.log(err)
        })
}

export const getQuestionCount = cid => {
    return axios
	.get('users/profile/classQuestionCount', {
	    classid: cid
	})
	.then(res =>{
	    console.log("QuestionCount: "+res.data)
	    return res.data
	})
	.catch(err=>{
	    console.log("QuestionCount ERR: "+err)
	})
}

