import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import jwt_decode from 'jwt-decode'
import { Collapse, Navbar as Navbar1, NavbarToggler, NavbarBrand, Nav, NavItem, NavLink } from 'reactstrap';
class Navbar extends Component {
    constructor() {
        super()
        this.state = {
            collapsed: true,
            setCollapsed: true
        }
        this.toggleNavbar = this.toggleNavbar.bind(this)
        // const toggleNavbar = () => setCollapsed(!collapsed);
    }
    toggleNavbar() {
        this.setState({ collapsed: !this.state.collapsed })
    }
    logOut(e) {
        e.preventDefault()
        localStorage.removeItem('usertoken')
        this.props.history.push('/')
    }

    render() {
        const loginRegLink = (
            <div>
                <NavItem>
                    <NavLink href="/login">
                        Login
                 </NavLink>
                </NavItem>
                <NavItem>
                    <NavLink href="/register">
                        Register
                 </NavLink>
                </NavItem>
            </div>
        )
        const userLink = (
            <div>
                <NavItem>
                    <NavLink href="/profile">
                        User
                    </NavLink>
                </NavItem>
                {/* <NavItem>
                    <NavLink href="/survey">
                        Take Survey
                    </NavLink>
                </NavItem> */}
                <NavItem>
                    <NavLink href="/" onClick={this.logOut.bind(this)}>
                        Logout
                    </NavLink>
                </NavItem>
            </div>
        )
        const derekLink = (
            <div>
                <NavItem>
                    <NavLink href="/profile">
                        User
                </NavLink>
                </NavItem>
                <NavItem>
                    <NavLink href="/datatables">
                        Data Tables
                </NavLink>
                </NavItem>
                <NavItem>
                    <NavLink to="/addClass">
                        Add Class
                </NavLink>
                </NavItem>
                <NavItem>
                    <NavLink to="/addTeacher">
                        Add Teacher
                </NavLink>
                </NavItem>
                <NavItem>
                    <NavLink href="/" onClick={this.logOut.bind(this)}>
                        Logout
                </NavLink>
                </NavItem>
            </div>
        )
        const teacherLink = (
            <div>
                <NavItem>
                    <NavLink href="/profile">
                        User
                </NavLink>
                </NavItem>
                <NavItem>
                    <NavLink href="/addClass">
                        Add Class
                </NavLink>
                </NavItem>
                <NavItem>
                    <NavLink href="/" onClick={this.logOut.bind(this)}>
                        Logout
                </NavLink>
                </NavItem>
            </div>
        )

        return (
            <div>
                <Navbar1 color="faded" light>
                    <NavbarBrand href="/" className="mr-auto">AMPD</NavbarBrand>
                    <NavbarToggler onClick={this.toggleNavbar} className="mr-2" />
                    <Collapse isOpen={!this.state.collapsed} navbar>
                        <Nav navbar>
                            <NavItem>
                                <NavLink href="/">Home</NavLink>
                            </NavItem>
                            {localStorage.usertoken ?
                                jwt_decode(localStorage.usertoken).accountType === '2' ? teacherLink :
                                    jwt_decode(localStorage.usertoken).accountType === '3' ? derekLink :
                                        userLink :
                                loginRegLink}
                        </Nav>
                    </Collapse>
                </Navbar1>
            </div>
            // <nav>
            //     {/* below div is where collapse and such goes 
            //  above is where the toggle, target and span go.
            //  */}
            //     <div id="navbar1">
            //         <ul>
            //             <li>
            //                 <Link to="/">
            //                     Home
            //          </Link>
            //             </li>
            //         </ul>
            //         {/* make a more elegant solution here? */}

            //     </div>
            // </nav>


        )
    }
}

export default withRouter(Navbar)
