        const join_class = (
            <div>
                <tr>
                    <td><div>
                        <input type="text"
                            name="class_code"
                            placeholder="Class Code"
                            value={this.state.class_code}
                            onChange={this.onChange}
                        />
                    </div></td>
                </tr>
                <tr>
                    <td><button onClick={this.joinClass}>Join Class</button></td>
                </tr>
            </div>
        )
