import React, { Component } from 'react'
import { CSVLink } from 'react-csv'

import { getDataTable } from './UserFunctions'

class DataTables extends Component{
	respData = ''

	/*constructor() {
		super()
	}*/

	async getTable(cid){
		this.respData = await getDataTable(cid)
		this.setState(this.respData.data)
	}

	async componentDidMount(){
		await this.getTable(2)
	}

	render(){
		return(
			<div>
				{this.respData.data ?
					<p><CSVLink
						data={this.respData.data}
						filename={'ampdTable.csv'}
						enclosingCharacter={``}>Download</ CSVLink> Table</p>
				: <p>Download Table</p>}
			</div>
		)
	}
}

export default DataTables
